          <router-link  v-if="$store.state.isauthenticated" class="تسجيلالدخول1 " to="/"   @click="Logout" style="text-decoration: none;">تسجيل
            الخروج</router-link>
            
        </div>
        <router-link  v-if="$store.state.isauthenticated" class="المستخدم" to="/Profile"    style="text-decoration: none;">{{arabic_last_name}} {{arabic_first_name}}</router-link>
