<script>

export default {
  name: 'NavBar',
  data() {
        return {
            arabic_first_name: '',
            arabic_last_name:'',
            
        }
    },
  props: {
    
  },
  beforeCreate(){
    this.$store.commit("initializeStore")
    
  },
  mounted() {
      setInterval(()=> this.arabic_first_name= localStorage.getItem("arabic_first_name"),
            this.arabic_last_name=localStorage.getItem("arabic_last_name")
      ,500)  
    },
    methods: {
      Logout(){
        localStorage.removeItem('access');
        localStorage.removeItem('refresh');
        localStorage.removeItem('userid');
        localStorage.removeItem('is_staff');
        localStorage.removeItem('username');
        localStorage.removeItem('arabic_first_name');
        localStorage.removeItem('arabic_last_name');
        localStorage.setItem("isauthenticated",false)
        this.$store.commit("initializeStore")
        this.$router.push("/Login")
      }
    }
}
</script>
