<template>
    <main>
      <h1>مادة اللغة العربية</h1>
  
      <!-- For Component View -->
      <div id="meetingSDKElement">
        <!-- Zoom Meeting SDK Component View Rendered Here -->
      </div>
  
      <button @click="getSignaturestart">إبدأ الدرس</button>
    </main>
  </template>
  
  <script>
  
  const KJUR = require('jsrsasign')
   const iat = Math.round(new Date().getTime() / 1000) - 30;
    const exp = iat + 60 * 60 * 2
  
    const oHeader = { alg: 'HS256', typ: 'JWT' }
  
    const oPayload = {
      sdkKey: 'uED7ms6wdGDtwWBCYR634XIFZd8P6NCvJ5mz',
      mn: 5872541577,
      role: 1,
      iat: iat,
      exp: exp,
      appKey: 'wwNrKgAOZ43Exyo6dVwvz1hEwjf6idSsgfve',
      tokenExp: iat + 60 * 60 * 2
    }
  
    const sHeader = JSON.stringify(oHeader)
    const sPayload = JSON.stringify(oPayload)
    const signaturestart = KJUR.jws.JWS.sign('HS256', sHeader, sPayload, 'wwNrKgAOZ43Exyo6dVwvz1hEwjf6idSsgfve')
    localStorage.setItem("signaturestart",signaturestart)
  import axios from "axios";
  import ZoomMtgEmbedded from '@zoomus/websdk/embedded';
  
  export default {
    name: 'StartMeeting',
    created () {
    },
    data () {
      return {
        windowHeight: window.innerHeight,
        windowWidth: window.innerWidth,
        client: ZoomMtgEmbedded.createClient(),
        // This Sample App has been updated to use SDK App type credentials https://marketplace.zoom.us/docs/guides/build/sdk-app
        sdkKey: "uED7ms6wdGDtwWBCYR634XIFZd8P6NCvJ5mz",
        meetingNumber: "5872541577",
        passWord: "MsS5vK",
        role: 1,
        signatureEndpoint: "http://localhost:4000",
        userEmail: "",
        userName: "الأستاذ" + ' ' + localStorage.getItem("arabic_last_name") + ' ' + localStorage.getItem("arabic_first_name") ,
        // pass in the registrant's token if your meeting or webinar requires registration. More info here:
        // Meetings: https://marketplace.zoom.us/docs/sdk/native-sdks/web/component-view/meetings#join-registered
        // Webinars: https://marketplace.zoom.us/docs/sdk/native-sdks/web/component-view/webinars#join-registered
        registrantToken: '',
  
  
        
  
      }
    },
    mounted() {
      this.$nextTick(() => {
        window.addEventListener('resize', this.onResize);
      })
    },
    beforeUnmount() { 
      window.removeEventListener('resize', this.onResize); 
    },
    methods: {
      onResize() {
        this.windowHeight = window.innerHeight
        this.windowWidth = window.innerWidth
      },
      getSignature() {
        axios.post(this.signatureEndpoint, {
          meetingNumber: this.meetingNumber,
          role: this.role
        })
        .then(res => {
          console.log(res.data.signature);
          this.startMeeting(res.data.signature);
        })
        .catch(error => {
          console.log(error);
        });
      },
      getSignaturestart() {
        this.startMeeting(localStorage.getItem("signaturestart"));
      },
      startMeeting(signature) {
        let meetingSDKElement = document.getElementById('meetingSDKElement');
  
        this.client.init({
          debug: true,
          zoomAppRoot: meetingSDKElement,
          language: 'en-US',
          customize: {
            video:{
        //       popper: {
        //   disableDraggable: true
        // },
              isResizable: true,
              viewSizes: {
                default: {
                width: this.windowWidth - this.windowWidth*0.33 ,
                // width: 1000,
                height: 500

              },
              ribbon: {
                width: this.windowWidth - this.windowWidth*0.33 ,
                // width: 1000,
                height:500
              }
              }
            },
            meetingInfo: ['host'],
            toolbar: {
              buttons: [
                {
                  text: 'Custom Button',
                  className: 'CustomButton',
                  onClick: () => {
                    console.log('custom button');
                  }
                }
              ]
            }
          }
        });
  
        this.client.join({
          sdkKey: this.sdkKey,
          signature: signature,
          meetingNumber: this.meetingNumber,
          password: this.passWord,
          userName: this.userName,
          userEmail: this.userEmail,
          tk: this.registrantToken
        })
      }
    }
  }
  </script>
  
