<template>
    <div class="v31_173">
        <div class="v31_174"></div>
        <form class="v31_206" @submit.prevent="submitForm">
            
            <div class="v31_207">
                <div class="v31_208">
                    <div>
                        <input class="v31_209" type="text" id="username" name="username" v-model="username"
                            placeholder="   أدخل إسم المستخدم أو بريدك الإلكتروني">
                    </div>
                </div><span class="v31_210"> إسم المستخدم أو البريد الإلكتروني :</span>
            </div>
            <div class="v31_211">
                <div class="v31_212">
                    <div>
                        <input class="v31_213" type="password" name="password" v-model="password" placeholder="   أدخل كلمة السر">
                    </div>

                </div><span class="v31_214"> كلمة السر :</span><span class="v31_215">نسيت كلمة السر ؟</span>
                <span class= "v169_378" :class="{ hidden: ishidden }" >المعلومات التي قمت بإدخالها خاطئة</span>
            </div>
            <div class="v31_217">
                <div class="v31_218">
                    <button class="v31_219" type="submit" name="signin_button" id="mybutton"><span
                        class="v31_220"  Cursor="Arrow">تسجيل الدخول</span></button>
                </div>
            </div><router-link class="v31_221" to="/Signin" style="text-decoration: none;">ليس لديك حساب؟ سجل اﻵن</router-link>
            
        </form>
        

        
        <span class="v31_205">ﺳﺠﻞ ﻣﻌﻨﺎ لتفهم دروﺳﻚ و ﺗﺮاﺟﻊ ﻟﻠﺈﺧﺘﺒﺎرات
        </span>
    </div>
    <div  class="IMAGE4"></div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'LoginView',
    data: () => ({
    username: '',
    password: '',
    ishidden: false,
    ishidden1: false,
    ishidden2: true,
    ishidden3: false,
    email_id: null,
    errors: [],
  }),
  beforeCreate(){
    this.$store.commit("initializeStore")
    const isauthenticated = this.$store.state.isauthenticated
    if (isauthenticated) {
        this.$router.push("/Profile")
    }
  },
    created(){
       let htmlEl=document.querySelector("html");
        htmlEl.setAttribute('dir','rtl');
        htmlEl.setAttribute('lang','ar');
      },
    methods:{
      submitForm() {
        axios.defaults.headers.common['Authorization']=''
        localStorage.removeItem("access")

        const formData={
          username: this.username,
          password: this.password
        }
        axios 
          .post('/api/v1/jwt/create/', formData)
          .then(response => {
            console.log(response.data)
            const access= response.data.access
            const refresh= response.data.refresh
            this.$store.commit('setAccess', access)
            this.$store.commit('setRefresh', refresh)
            this.$store.commit('setUsername', this.username)
            axios.defaults.headers.common['Authorization']= "JWT" + access
            localStorage.setItem("access",access)
            localStorage.setItem("refresh",refresh)
            localStorage.setItem("username",this.username) 
            localStorage.setItem("isauthenticated",true)
            this.$store.commit("initializeStore")
          })
          .catch(error => {
            console.log(error)
          })
          const access = this.$store.state.access
          axios
            .get("/api/v1/User/?username=" + this.username,{credentials: 'omit'},{headers: {
                
                    'Authorization': "JWT" + access },})
    .then(response  => {
                    console.log(response.data)
                    
                    this.$store.commit('setUserid', response.data[0].id)
                    this.$store.commit('setIs_staff', response.data[0].is_staff)
                    localStorage.setItem("userid",response.data[0].id)
                    localStorage.setItem("is_staff",response.data[0].is_staff)
                    console.log(response.data[0].is_staff)
                    this.$store.commit("initializeStore")
                    this.$router.push("/Profile")
                    
                }).catch(error =>{
                    console.log(error)
                })
      },
      
    }
}
</script>

