<template>
<span class="title1">السـنـةالـثـالـثـة إبـتـدائـي</span><br><br><br>
  <span id="text" v-if= "this.$store.state.is_staff==='false'" :src="arabic_last_name"> مرحبا بالتلميذ {{arabic_last_name}} {{arabic_first_name}}</span>
  <span id="text" v-if="this.$store.state.is_staff==='true'" :src="arabic_last_name" > مرحبا بالأستاذ {{arabic_last_name}} {{arabic_first_name}}</span>
<JoinMeeting v-if= "this.$store.state.is_staff==='false'"></JoinMeeting>
<StartMeeting v-if="this.$store.state.is_staff==='true'"></StartMeeting>
</template>

<script>

import JoinMeeting from '@/components/JoinMeeting.vue'
import StartMeeting from '@/components/StartMeeting.vue'
export default {
    name: 'ProfileView',
    components: {
        JoinMeeting,
        StartMeeting
  },
    data() {
        return {
            arabic_first_name: '',
            arabic_last_name:'',
            user:'admin',
            is_staff:''
        }
    },
    beforeCreate(){
    this.$store.commit("initializeStore")
    const isauthenticated = localStorage.getItem("isauthenticated")
    
    if (!isauthenticated) {
        this.$router.push("/Login")
    }
  },
    created(){
       let htmlEl=document.querySelector("html");
        htmlEl.setAttribute('dir','rtl');
        htmlEl.setAttribute('lang','ar');
        this.is_staff= localStorage.getItem("is_staff")
      },
    mounted() {
        
        this.getme()
    },
    methods: {
        
        getme(){
            this.$store.commit("initializeStore")
            const access = this.$store.state.access
            fetch("https://ana3ab9ari.pythonanywhere.com/api/v1/USER/?user=" + localStorage.getItem("userid"),{credentials: 'omit'},{headers: {
                    'Authorization': "JWT" + access
    },}).then(res => res.json()).then(data => {
                    console.log(data)
                    this.arabic_first_name = data[0].arabic_first_name 
                    localStorage.setItem("arabic_first_name",data[0].arabic_first_name)
                    this.arabic_last_name = data[0].arabic_last_name
                    localStorage.setItem("arabic_last_name",data[0].arabic_last_name)
                }).catch(error =>{
                    console.log(error.data)
                })
        }
    }

}
</script>

